import { Paper } from "@mui/material";
import React from "react";

const FoodFact = ({
  carb,
  protein,
  fat,
  iron,
  magnesium,
  potassium,
  zinc,
  sodium,
  calcium,
}) => {
  const macro = ["Carboidrato", "Protena", "Gordura"];
  const macroValue = carb !== "NA" ? [carb, protein, fat] : [0, protein, fat];
  const maxMacro = macroValue.reduce((total, item) => {
    return Math.max(total, item);
  }, 0);
  const macroIndex = macroValue.indexOf(maxMacro);

  const mineralValue = [iron, magnesium, potassium, sodium, calcium, zinc];
  const mineral = ["Ferro", "Magnsio", "Potssio", "Sdio", "Clcio", "Zinco"];
  const newValues = [];
  for (let i = 0; i < mineralValue.length; i++) {
    mineralValue[i] == "Tr"
      ? newValues.push(0)
      : newValues.push(mineralValue[i]);
  }
  const maxMineral = newValues.reduce((total, item) => {
    return Math.max(total, item);
  }, 0);
  const mineralIndex = newValues.indexOf(maxMineral);

  const carbFact =
    "Ao contrrio da crena popular, carboidratos no engordam. Na verdade, os carboidratos so fonte de energia e so indispensveis para um bom rendimento na prtica de esportes de alta intensidade. ";
  const protFact =
    "As protenas so as responsveis pelo desenvolvimento e manunteno dos rgos e tecidos. Por serem responsveis pelo reparo do tecido muscular, as protenas so um macronutriente essencial para praticantes de musculao, os mesmos devem ter uma alta ingesto de protenas diariamente. ";
  const fatFact =
    "Apesar de ser vista como vil, a gordura tambm  parte essencial da dieta, pois desempenha importante papel na produo de hormnios e o transporte de algumas vitaminas. Como a gordura  o macronutriente mais calrico, seu consumo deve ser mais limitado em relao s protenas e carboidratos.";

  const macroFacts = [carbFact, protFact, fatFact];

  const ironFact = (
    <div>
      <p>
        Essencial para o funcionamento do organismo, sendo uma de suas
        principais funes o transporte de oxignio para os demais rgos. A
        carncia de ferro  uma das carncias mais comuns do mundo, podendo
        resultar em anemia.
      </p>
      <p>
        <span className="text-lg 2xl:text-xl font-bold">Fontes:</span> Feijo,
        fgado de boi, fgado de frango, pitaia.
      </p>
    </div>
  );

  const magFact = (
    <div>
      <p>
        Um dos principais micronutrientes dentro das clulas. No msculo, 
        responsvel pela contrao e relaxamento, presso sangunea, comunicao
        com o sistema nervoso, entre outros. Alguns sintomas tpicos de carncia
        so anorexia, letargia e fraqueza.{" "}
      </p>
      <p>
        <span className="text-lg 2xl:text-xl font-bold">Fontes:</span> Feijo,
        soja, semente de chia, peito de frango, banana.
      </p>
    </div>
  );

  const potFact = (
    <div>
      <p>
         o mineral em maior quantidade na clula, sendo assim,  responsvel
        pelo bom funcionamento da mesma. A carncia grave de potssio est
        associada com o aumento da presso arterial, aumentando o risco de
        doenas cardiovasculares.
      </p>
      <p>
        <span className="text-lg 2xl:text-xl font-bold">Fontes:</span> Feijo,
        quinoa, pistache, leite de vaca desnatado.
      </p>
    </div>
  );

  const sodiumFact = (
    <div>
      <p>
         responsvel pela manunteno da presso sangunea. 40% do sal de
        cozinha  composto de sdio, portanto  a fonte mais comum na
        alimentao. Por ser facilmente encontrado, a carncia de sdio  rara,
        mas pode ocorrer em indivduo que suam muito, como trabalhadores braais
        ou atletas em ambientes quentes. J o excesso pode levar ao aumento da
        presso arterial e assim levando ao desenvolvimento de doenas
        cardiovasculares.
      </p>
      <p>
        <span className="text-lg 2xl:text-xl font-bold">Fontes:</span> Semente
        de chia, clara do ovo de galinha, morango, feijo.
      </p>
    </div>
  );

  const calcFact = (
    <div>
      <p>
         o mineral mais abundante no organismo, representando de 1 a 2% do peso
        corporal. Possui funes estruturais e funcionais, como o
        desenvolvimento e manunteno do esqueleto e fortalecimento dos ossos e
        dentes. Na verdade, 99% do clcio  encontrado nos ossos e dentes. A
        carncia leva ao raquitismo e osteomalcia.
      </p>
      <p>
        <span className="text-lg 2xl:text-xl font-bold">Fontes:</span> Lambari,
        leite de vaca desnatado, soja, feijo, semente de chia.
      </p>
    </div>
  );

  const zincFact = (
    <div>
      <p>
        Desempenha funes catalticas, quebra de molculas, assim acelerando a
        reao. Alm disso tambm est envolvido em uma srie de processos, que
        vo desde sntese do DNA at a cicatrizao de feridas. A carncia pode
        levar a anorexia, perda de paladar, alopecia, entre outros.
      </p>
      <p>
        <span className="text-lg 2xl:text-xl font-bold">Fontes:</span> Ostra,
        acm bovino, gro-de-bico, ovo de galinha, semente de chia.
      </p>
    </div>
  );

  const mineralFacts = [
    ironFact,
    magFact,
    potFact,
    sodiumFact,
    calcFact,
    zincFact,
  ];

  return (
    <div className="w-full lg:w-1/3 mt-2 mb-16">
      <Paper elevation={12}>
        <div className="w-full p-3">
          <h1 className="text-lg 2xl:text-xl font-sans font-normal text-stone-800">
            Macronutriente mais abundante:
          </h1>
          <p className="text-xl 2xl:text-2xl font-sans font-bold leading-3">
            {macro[macroIndex]}
          </p>
          <p className="text-base 2xl:text-lg text-justify">
            {macroFacts[macroIndex]}
          </p>
        </div>
      </Paper>
      <Paper elevation={12}>
        <div className="w-full p-5 mt-2">
          <h1 className="text-lg 2xl:text-xl font-sans font-normal text-stone-800">
            Mineral mais abundante:{" "}
          </h1>
          <p className="text-xl 2xl:text-2xl font-sans font-bold leading-3">
            {mineral[mineralIndex]}
          </p>
          <p className="text-base  2xl:text-lg text-justify">
            {mineralFacts[mineralIndex]}
          </p>
        </div>
      </Paper>
    </div>
  );
};

export default FoodFact;
